import React from 'react';
import {Chrono} from "react-chrono";


export default function BasicTimeline() {
    const items = [{
        title: "October 2013",
        cardTitle: "Adobe",
        cardSubtitle: "Impact: 153 million user records",
        cardDetailedText: ` As reported in early October of 2013 by security blogger Brian Krebs, Adobe originally reported that hackers had stolen nearly 3 million encrypted customer credit card records, plus login data for an undetermined number of user accounts.

Later that month, Adobe raised that estimate to include IDs and encrypted passwords for 38 million “active users.” Krebs reported that a file posted just days earlier “appears to include more than 150 million username and hashed password pairs taken from Adobe.” Weeks of research showed that the hack had also exposed customer names, IDs, passwords and debit and credit card information.

An agreement in August 2015 called for Adobe to pay a $1.1 million in legal fees and an undisclosed amount to users to settle claims of violating the Customer Records Act and unfair business practices. In November 2016, the amount paid to customers was reported at $1 million.`,
    },
{
        title: "October 2016",
        cardTitle: "Adult Friend Finder",
        cardSubtitle: "Impact: 412.2 million accounts",
        cardDetailedText: `This breach was particularly sensitive for account holders because of the services the site offered. The FriendFinder Network, which included casual hookup and adult content websites like Adult Friend Finder, Penthouse.com, Cams.com, iCams.com and Stripshow.com, was breached in mid-October 2016. The stolen data spanned 20 years on six databases and included names, email addresses and passwords.`,
    },
        {
        title: "May 2019",
        cardTitle: "Canva",
        cardSubtitle: "Impact: 137 million user accounts",
        cardDetailedText: `In May 2019 Australian graphic design tool website Canva suffered an attack that exposed email addresses, usernames, names, cities of residence, and salted and hashed with bcrypt passwords (for users not using social logins — around 61 million) of 137 million users. Canva says the hackers managed to view, but not steal, files with partial credit card and payment data.`,
    },

        ];

    return (
        <div className="center">
            <Chrono
                items={items}
                mode="VERTICAL_ALTERNATING"
                hideControls="0"
            >
            </Chrono>
        </div>
    );
}
